 import {_get,_post,_put,_delete} from "../../../api/index.js"
 export default {
    data() {
      return {
        id:'',
        name:'',
        status:'',
        is_manager:'',
        checkedArr:[4],
        data: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      };
    },
     created(){
      this.getTreeData()
    },
    methods: {
      getTreeData(){
        let str=this.$route.query.permissions;
        let arr=str.split(",");
        let newArr=arr.map(Number);
        this.checkedArr=newArr;
        this.id = this.$route.query.id;
        this.name=this.$route.query.name;
        this.status=this.$route.query.status;
        this.is_manager=this.$route.query.is_manager;
        let params={name:this.name,status:this.status,is_manager:this.is_manager};
        _get("api/permission/tree",params).then(res => {
           this.data=res.data.data;
        })
      },
      getCheckedNodes() {
        console.log(this.$refs.tree.getCheckedNodes());
      },
      getCheckedKeys() {
         //console.log(this.$refs.tree.getCheckedKeys());
        let arr=this.$refs.tree.getCheckedKeys();
        let params={permissions:arr}
        _post("api/role/assign/"+this.id,params).then(res=>{
            this.$message({
              message: "提交成功",
              type: "success"
            });
            this.$router.push('/roleList');
        }).catch(error=>{
          this.$message({
            message: "提交失败",
            type: "error"
          });
        });
      },
      setCheckedNodes() {
        this.$refs.tree.setCheckedNodes([{
          id: 5,
          label: '二级 2-1'
        }, {
          id: 9,
          label: '三级 1-1-1'
        }]);
      },
      setCheckedKeys() {
        this.$refs.tree.setCheckedKeys([3]);
      },
      resetChecked() {
        this.$refs.tree.setCheckedKeys([]);
      }
 
    },
    watch:{
    "$route":"getTreeData"
   }
  };